export default {
	data() {
		return {
			userActivityInfo: {
				username: "天堂屠夫",
				total_visits: 0.,
				userActivityNum: {
					num1: 0,
					num2: 0,
					num3: 0,
					num4: 0
				},
				avatar: "../../static/test/1.jpg"
			},
			advertisements: [
				'../../static/test/1.jpg', '../../static/test/timg.jpg', '../../static/test/3.png'
			]

		}
	},
	methods: {
		open() {
			this.$refs.popup.open()
		},
		navtoChangePerson() {
			let isLogin = this.checkLogin('../self/self', '2')
			console.log(isLogin)
			if (!isLogin) {
				uni.showModal({
					content: '是否要保存为草稿?',
					cancelText: '不保存',
					confirmText: '保存',
					success: res => {
						if (res.confirm) {
							console.log('保存');
						} else {
							console.log('不保存');
						}
						this.isget = true;
						uni.navigateBack({
							delta: 1
						});
					},

				})
				this.open()
			} else {
				uni.navigateTo({
					url: "../changePerson/changePerson",
					"animationType": "fade-in",
					"animationDuration": 300
				})
			}

		},
		toDetail() {
			let isLogin = this.checkLogin('../self/self', '2')
			if (!isLogin) {
				uni.setStorageSync('storage_key', 'hello');
				uni.navigateTo({
					url: '../personalDetail/personalDetail',
					"animationType": "fade-in",
					"animationDuration": 300

				})
			}

		}
	},
	async onLoad() {
		let res = await this.request('me/', {
			uid: 1
		})
	}
}
